.gpt3__blog-container__article {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background: #031B34;
}
.gpt3__blog-container__article-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 1rem 1.5rem;
  height: 100%;
}
.gpt3__blog-container__article-content p {
  font-family: "Manrope", sans-serif;
  color: #fff;
  font-size: 12px;
  font-weight: bold;
  line-height: 35px;
}
.gpt3__blog-container__article-content p:last-child {
  cursor: pointer;
}
.gpt3__blog-container__article-content h3 {
  font-family: "Manrope", sans-serif;
  font-weight: 800;
  line-height: 30px;
  font-size: 25px;
  color: #fff;
  margin-bottom: 5rem;
  cursor: pointer;
}
.gpt3__blog-container__article-image {
  width: 100%;
  height: 100%;
  background-color: #031B34;
}
.gpt3__blog-container__article-image img {
  width: 100%;
  height: 100%;
}

@media (max-width: 550px) {
  .gpt3__blog-container__article-content h3 {
    font-size: 18px;
    line-height: 25px;
  }
}
.gpt3__blog {
  display: flex;
  flex-direction: column;
}
.gpt3__blog-heading {
  width: 100%;
  text-align: left;
  margin-bottom: 5rem;
}
.gpt3__blog-heading h1 {
  font-family: "Manrope", sans-serif;
  font-weight: 800;
  font-size: 62px;
  line-height: 75px;
}
.gpt3__blog-container {
  display: flex;
  flex-direction: row;
}
.gpt3__blog-container_groupA {
  flex: 0.75;
  margin-right: 2rem;
}
.gpt3__blog-container_groupB {
  flex: 1;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 2rem;
}

@media (max-width: 1050px) {
  .gpt3__blog-container {
    flex-direction: column-reverse;
  }
  .gpt3__blog-container_groupA {
    margin: 2rem 0;
  }
  .gpt3__blog-container_groupA .gpt3__blog-container__article {
    width: 48%;
  }
  .gpt3__blog-container_groupA .gpt3__blog-container__article-image {
    height: 250px;
  }
}
@media (max-width: 700px) {
  .gpt3__blog-container_groupB {
    grid-template-columns: repeat(1, 1fr);
  }
  .gpt3__blog-container_groupA .gpt3__blog-container__article {
    width: 100%;
  }
  .gpt3__blog-heading h1 {
    font-size: 46px;
    line-height: 52px;
  }
}
@media (max-width: 550px) {
  .gpt3__blog-heading h1 {
    font-size: 34px;
    line-height: 42px;
  }
}
.gpt3__brand {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.gpt3__brand div {
  flex: 1;
  max-width: 150px;
  min-width: 120px;
  margin: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.gpt3__cta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  padding: 2rem;
  margin: 4rem;
  border-radius: 1rem;
  background: linear-gradient(103.22deg, #AE67FA -13.86%, #F49867 99.55%);
}
.gpt3__cta-content p {
  font-family: "Manrope", sans-serif;
  font-size: 12px;
  line-height: 30px;
  color: #0e0e0e;
}
.gpt3__cta-content h3 {
  font-family: "Manrope", sans-serif;
  font-weight: 800;
  font-size: 24px;
  line-height: 45px;
}
.gpt3__cta-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 2rem;
}
.gpt3__cta-btn button {
  border-radius: 40px;
  background: black;
  font-family: "Manrope", sans-serif;
  font-weight: bold;
  font-size: 18px;
  line-height: 30px;
  color: #fff;
  padding: 0.5rem 1rem;
  border: none;
  outline: none;
  cursor: pointer;
  min-width: 150px;
}

@media (max-width: 700px) {
  .gpt3__cta {
    flex-direction: column;
  }
  .gpt3__cta-btn {
    margin: 2rem 0 0;
  }
}
@media (max-width: 550px) {
  .gpt3__cta {
    margin: 4rem 2rem;
  }
  .gpt3__cta-content {
    font-size: 18px;
    line-height: 32px;
  }
  .gpt3__cta-btn button {
    font-size: 14px;
    line-height: 28px;
  }
}
.gpt3__features {
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
}
.gpt3__features-heading {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  text-align: left;
  margin-right: 5rem;
}
.gpt3__features-heading h1 {
  font-family: "Manrope", sans-serif;
  font-weight: 800;
  font-size: 2.4rem;
  line-height: 45px;
}
.gpt3__features-heading p {
  font-family: "Manrope", sans-serif;
  font-size: 16px;
  line-height: 30px;
  color: #ff8a71;
  margin-top: 2rem;
}
.gpt3__features-container {
  flex: 1.5;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
}

@media (max-width: 1050px) {
  .gpt3__features {
    flex-direction: column;
  }
  .gpt3__features-heading {
    margin: 0 0 2rem 0;
  }
}
@media (max-width: 550px) {
  .gpt3__features-heading h1 {
    font-size: 28px;
    line-height: 38px;
  }
}
.feature-compact {
  width: max-content !important;
  flex-direction: column !important;
}

.gpt3__features-container__feature {
  width: max-content;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-direction: row;
  margin: 1rem;
  width: 100%;
}
.gpt3__features-container__feature-title {
  flex: 1;
  max-width: 300px;
  margin-right: 2rem;
  margin-bottom: 1rem;
}
.gpt3__features-container__feature-title h1 {
  font-weight: 800;
  font-family: "Manrope", sans-serif;
  line-height: 35px;
  font-size: 18px;
  letter-spacing: -0.04em;
  color: #fff;
  display: block;
}
.gpt3__features-container__feature-title div {
  width: 38px;
  height: 3px;
  background: linear-gradient(103.22deg, #AE67FA -13.86%, #F49867 99.55%);
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  margin-bottom: 0.25rem;
}
.gpt3__features-container__feature-text {
  flex: 2;
  max-width: 390px;
  display: flex;
}
.gpt3__features-container__feature-text p {
  font-family: "Manrope", sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 24px;
  color: #81AFDD;
}

@media (max-width: 550px) {
  .gpt3__features-container__feature {
    margin: 1rem 0;
  }
  .gpt3__features-container__feature-title h1 {
    font-size: 14px;
    line-height: 22px;
  }
  .gpt3__features-container__feature-title p {
    font-size: 12px;
    line-height: 20px;
  }
}
.gpt3__footer {
  display: flex;
  color: white;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #031B34;
}
.gpt3__footer-copyright {
  margin-top: 2rem;
  text-align: center;
  width: 100%;
}
.gpt3__footer-copyright p {
  font-family: "Manrope", sans-serif;
  font-size: 12px;
  line-height: 14px;
  color: white;
}
.gpt3__footer-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  border: 1px solid white;
  text-align: center;
  margin-bottom: 10rem;
  cursor: pointer;
}
.gpt3__footer-btn a {
  border: 1px;
  font-family: "Manrope", sans-serif;
  font-size: 18px;
  line-height: 21px;
  color: white;
}
.gpt3__footer-links {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  flex-direction: row;
  width: 100%;
  text-align: left;
}
.gpt3__footer-links__div {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
}
.gpt3__footer-links__div h4 {
  font-family: "Manrope", sans-serif;
  font-size: 14px;
  line-height: 16px;
  color: white;
  margin-bottom: 1rem;
  font-weight: bold;
}
.gpt3__footer-links__div p {
  font-family: "Manrope", sans-serif;
  font-size: 12px;
  line-height: 14px;
  color: white;
  margin: 0.5rem 0;
  cursor: pointer;
}
.gpt3__footer-links__logo {
  display: flex;
  flex-direction: column;
}
.gpt3__footer-links__logo img {
  width: 118px;
  height: 30px;
  margin-bottom: 1rem;
}
.gpt3__footer-links__logo p {
  font-family: "Manrope", sans-serif;
  font-size: 12px;
  line-height: 16px;
  color: white;
}
.gpt3__footer-links div {
  width: 250px;
  margin: 1rem;
}
.gpt3__footer-heading {
  width: 100%;
  text-align: center;
  margin-bottom: 3rem;
}
.gpt3__footer-heading h1 {
  font-family: "Manrope", sans-serif;
  font-weight: 800;
  font-size: 68px;
  line-height: 75px;
  text-align: center;
}

@media (max-width: 1050px) {
  .gpt3__footer-heading h1 {
    font-size: 44px;
    line-height: 50px;
  }
}
@media (max-width: 550px) {
  .gpt3__footer-heading h1 {
    font-size: 34px;
    line-height: 42px;
  }
  .gpt3__footer-links div {
    margin: 1rem 0;
  }
  .gpt3__footer-btn p {
    font-size: 14px;
    line-height: 20px;
  }
}
.gpt3__header {
  display: flex;
}
.gpt3__header-content {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  margin-right: 5rem;
}
.gpt3__header-content h1 {
  font-family: "Manrope", sans-serif;
  font-weight: 800;
  font-size: 3rem;
  line-height: 75px;
  letter-spacing: -0.04em;
}
.gpt3__header-content p {
  font-family: "Manrope", sans-serif;
  font-weight: normal;
  font-style: normal;
  font-size: 20px;
  list-style: 27px;
  color: #81AFDD;
  margin-top: 1.5rem;
  line-height: 1.6rem;
}
.gpt3__header-content__input {
  width: 100%;
  margin: 2rem 0 1rem;
  display: flex;
}
.gpt3__header-content__input input {
  flex: 2;
  min-height: 50px;
  background: #031B34;
  border: 2px solid #031B34;
  padding: 0 1rem;
  outline: none;
  color: #fff;
  border-radius: 5px 0 0 5px;
  font-family: "Manrope", sans-serif;
  font-size: 20px;
  line-height: 27px;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}
.gpt3__header-content__input button {
  background: #ff4820;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  font-family: "Manrope", sans-serif;
  font-weight: bold;
  font-size: 20px;
  line-height: 27px;
  color: #fff;
  flex: 0.6;
  min-height: 50px;
  cursor: pointer;
  outline: none;
  padding: 0 1rem;
  border: none;
}
.gpt3__header-content__people {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-top: 2rem;
}
.gpt3__header-content__people img {
  width: 181.79px;
  height: 38px;
}
.gpt3__header-content__people p {
  font-family: "Manrope", sans-serif;
  font-size: 12px;
  line-height: 38px;
  color: #fff;
  margin-left: 1rem;
  text-align: center;
  margin: 0 0 0 1rem;
}
.gpt3__header-image {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}
.gpt3__header-image img {
  width: 100%;
  height: 100%;
}

@media (max-width: 1050px) {
  .gpt3__header {
    flex-direction: column;
  }
  .gpt3__header-content {
    margin: 0 0 3rem;
  }
}
@media (max-width: 700px) {
  .gpt3__header h1 {
    font-size: 48px;
    line-height: 60px;
  }
  .gpt3__header p {
    font-size: 16px;
    line-height: 24px;
  }
  .gpt3__header-content {
    margin: 0 0 3rem;
  }
  .gpt3__header-content__people {
    flex-direction: column;
  }
  .gpt3__header-content__people p {
    margin: 0;
  }
  .gpt3__header-content input,
.gpt3__header-content button {
    font-size: 16px;
    line-height: 24px;
  }
}
@media (max-width: 550px) {
  .gpt3__header h1 {
    font-size: 36px;
    line-height: 48px;
  }
  .gpt3__header p {
    font-size: 14px;
    line-height: 24px;
  }
  .gpt3__header-content__input input,
.gpt3__header-content__input button {
    font-size: 12px;
    line-height: 16px;
  }
}
#badApple {
  width: 100%;
}

